.timeline{
  /* counter-reset: year 2013; */
  position: relative;
  display: none;
}

.timeline li{
  list-style: none;
  float: left;
  width: 25%;
  position: relative;
  text-align: center;
  text-transform: uppercase;
}

ul:nth-child(1){
  color: #572E85;
}

.timeline li:before{
  /* counter-increment: time; */
  content: counter(time);
  width: 23px;
  height: 23px;
  border-radius: 50%;
  display: block;
  text-align: center;
  line-height: 50px;
  margin: 0 auto 10px auto;
  background: #FF1F00;
  color: rgba(0,255,255, 0);
  transition: all ease-in-out .3s;
  cursor: pointer;
}

.timeline li:after{
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: #572E85;
  top: 10px;
  left: -50%;
  z-index: -999;
  transition: all ease-in-out .3s;
}

.timeline li:first-child:after{
  content: none;
}
.timeline li.active{
  color: #572E85;
}
.timeline li.active:before{
  background:#FF1F00;
  color: rgba(0,255,255, 0);
}

.timeline li.active + li:after{
  background: #572E85;
}

